import { Dashboard } from "./components/Dashboard";
import { useState } from "react";
import Modal from 'react-modal'
import { Header } from "./components/Header";
import { GlobalStyled } from "./styles/global";
import { NewTransactionModal } from "./components/NewTransactionModal";
import { TransactionProvider } from "./hooks/UseTransactions";

Modal.setAppElement("#root");
export function App() {
  const [isNewTransactionOpenModal, setIsNewTransactionOpenModal] = useState(false);
  function handleOpenNewTransactionModal() {
    setIsNewTransactionOpenModal(true);
  }

  function handleCloseOpenNewTransactionModal() {
    setIsNewTransactionOpenModal(false)
  }
  return (
    <TransactionProvider>
      <Header onOpenNewTransactionModal={handleOpenNewTransactionModal} />
      <Dashboard />
      <NewTransactionModal
        isOpen={isNewTransactionOpenModal}
        onRequestClose={handleCloseOpenNewTransactionModal}
      />
      <GlobalStyled />
    </TransactionProvider>
  );
}


