import incomeImg from '../../assets/income.svg';
import autcomeImg from '../../assets/autCome.svg';
import totalImg from '../../assets/total.svg';

import { Conatiner } from "./style";
import { useTransactions } from '../../hooks/UseTransactions';

export function Summary() {
   const { transactions } = useTransactions();
   /* const totalDeposit = transactions.reduce((acc, transation) => {
      if(transation.type === 'deposit') {
      return acc + transation.amount;
      }
     
      return acc;
      }, 0);  
   */
  const summary = transactions.reduce((acc, transaction) => {
    if(transaction.type === 'deposit') {
      acc.deposits += transaction.amount
      acc.total += transaction.amount
    } else {
      acc.withdraws += transaction.amount
      acc.total -= transaction.amount
    }
    return acc;
  }, {
   deposits: 0,
   withdraws: 0,
   total: 0,
  })
  return(
   <Conatiner >
     <div>
        <header>
           <p>Entradas</p>
           <img src={incomeImg} alt="Entradas" />
        </header>
        <strong>
        {new Intl.NumberFormat('pt-BR', {
            style: 'currency',
            currency: 'AOA'
            }).format(summary.deposits)
         } 
        </strong>
     </div>
     <div>
        <header>
           <p>Sadas</p>
           <img src={autcomeImg} alt="Sadas" />
        </header>
        <strong>- 
        {new Intl.NumberFormat('pt-BR', {
            style: 'currency',
            currency: 'AOA'
            }).format(summary.withdraws)
         } 
       </strong>
     </div>
     <div className='hightLight-background'>
        <header>
           <p>Total</p>
           <img src={totalImg} alt="Total" />
        </header>
        <strong>
        {new Intl.NumberFormat('pt-BR', {
            style: 'currency',
            currency: 'AOA'
            }).format(summary.total)
         } 
       </strong>
     </div>
   </Conatiner>
  );
}