import { createContext, useState, useEffect, ReactNode, useContext } from 'react';
import { api } from '../services/api';

interface Transation {
  id: number;
  title: string;
  amount: number;
  type: string;
  category: string;
  createdAt: string
}
interface TransationProviderProps {
  children: ReactNode
}
type TransactionInput = Omit<Transation, 'id' | 'createdAt'>

interface TransactionsContextData {
  transactions: Transation[],
  createTransaction: (transation: TransactionInput) =>Promise<void>;
}

const TransactionContext = createContext<TransactionsContextData>(
  {} as TransactionsContextData
);

export function TransactionProvider({ children }: TransationProviderProps){
  const [transactions, setTransactions] = useState<Transation[]>([]);
    useEffect(() => { 
      api.get("transactions")
      .then(response => setTransactions(response.data.transactions))
     }, []);

  async function createTransaction(transactionInput: TransactionInput ) {
   const response = await api.post('/transactions', {
    ...transactionInput,
    createdAt: new Date()
   })
   const { transaction } = response.data;
   setTransactions([
    ...transactions,
    transaction,
   ])
  }

  return (
    <TransactionContext.Provider value={{ transactions, createTransaction}}>
       {children}
    </TransactionContext.Provider>
  )    
}

export function useTransactions() {
  const context = useContext(TransactionContext);
  return context;
}